import React, { Component } from 'react'

export default class Prices extends Component {
    state = {
        currency: 'USD'
    }
    render() {
        return (
            <div>
                {this.props.bpi.USD.description}
                {this.props.bpi.USD.code}
                {this.props.bpi.USD.rate}
            </div>
        )
    }
}
